/** XMLHttpRequest **/
global.XMLHttpRequest = require("xmlhttprequest").XMLHttpRequest

/** atob **/
var _atob = function (base64) {
  return Buffer.from(base64, 'base64').toString()
}
if (typeof window == 'undefined') window = {}
if (typeof window.atob == 'undefined') window.atob = _atob
if (typeof global.atob == 'undefined') global.atob = _atob

/** localStorage **/
var inMemoryLocalStorage = {};
global.localStorage = {
  getItem: function (k) {
    // console.log("localstorage: get " + k)
    return inMemoryLocalStorage[k];
  },
  setItem: function (k, v) {
    // console.log("localstorage: set " + k + "=" + v)
    inMemoryLocalStorage[k] = v;
  },
  removeItem: function (k) {
    // console.log("localstorage: rm " + k)
    delete inMemoryLocalStorage[k];
  },
  key: function(i) {
    return Object.keys(inMemoryLocalStorage)[i]
  }
}

const OneStore = require('onestore-client')
OneStore.ClientConstants.clientName = 'onestore-client-node'

module.exports = OneStore